import * as actionType from './actionType'
import {getData} from 'util'
import {API_REQUISITIONSORDERS_CREATE, API_REQUISITIONSORDERS_GETPRODUCTSTOCKBYCODE, API_REQUISITIONSORDERS_EXECUTE, API_REQUISITIONSORDERS_CLOSE, API_REQUISITIONSDTLORDERS, API_FIX_REQUISITIONWRITEOFF, API_CREATEREQUISITIONSORDERS_CREATE, API_REQUISITIONSORDERS_EXPORT, API_GET_MENUS_PERMISSIONS_BANS} from 'api'
//同步
export const update = (object) => ({
  type: actionType.REQUISITIONSORDERS_UPDATE,
  data: object
})
import moment from 'moment'

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  console.log(1111)
  dispatch({type: actionType.REQUISITIONSORDERS_UPDATE, data: {tableLoading: true}})
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  if (reqObj.initDate && reqObj.initDate.length) {
    reqObj.beginDate = moment(reqObj.initDate[0]).format('YYYY-MM-DD')
    reqObj.endDate = moment(reqObj.initDate[1]).format('YYYY-MM-DD')
  }
  delete reqObj.initDate
  getData({api: API_REQUISITIONSORDERS_CREATE, ...reqData}).then(data => {
    // 相应的逻辑判断
    let {page, size} = reqData.searchParams
    dispatch({
      type: actionType.REQUISITIONSORDERS_UPDATE,
      data: {
        page,
        size,
        tableLoading: false,
        total: data.result.total,
        selectedRowKeys: [], // 选中的队列key
        selectedRows: [], // 选中的队列数据
        searchResult: data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.REQUISITIONSORDERS_FILE
    })
  })
}

// 获取商品信息
export const handleGetGoodsInfo = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.REQUISITIONSORDERS_UPDATE})
  let api = API_REQUISITIONSORDERS_GETPRODUCTSTOCKBYCODE
  getData({api, ...reqData}).then(data => {
    console.log(data.code)
    // 相应的逻辑判断
    dispatch({
      type: actionType.REQUISITIONSORDERS_UPDATE,
      data: {
        // createTableLoading: false,
        codeNum: data.code
      }
    })
    callBack && callBack(data.result)
  }).catch(err => {
    dispatch({
      type: actionType.REQUISITIONSORDERS_FILE
    })
  })
}

// 执行-调成本中心
export const handleCarriedOut = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.REQUISITIONSORDERS_UPDATE, data: {tableLoading: true}})
  let api = API_REQUISITIONSORDERS_EXECUTE
  getData({api, ...reqData}).then(data => {
    console.log(data.code)
    // 相应的逻辑判断
    dispatch({
      type: actionType.REQUISITIONSORDERS_UPDATE,
      data: {
        isShowModal: false,
        codeNum: data.code
      }
    })
    callBack && callBack(data)
  }).catch(err => {
    dispatch({
      type: actionType.REQUISITIONSORDERS_FILE
    })
  })
}

// 取消领用单
export const handleCancel = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.REQUISITIONSORDERS_UPDATE, data: {modalTuttonLoading: true}})
  let api = API_REQUISITIONSORDERS_CLOSE
  getData({api, ...reqData}).then(data => {
    console.log(data)
    // 相应的逻辑判断
    if (data.code == 200) {
      console.log(data.result)
    }
    dispatch({
      type: actionType.REQUISITIONSORDERS_UPDATE,
      data: {
        modalTuttonLoading: false,
        isShowModal: false
      }
    })
    callBack && callBack(data.result)
  }).catch(err => {
    dispatch({
      type: actionType.REQUISITIONSORDERS_FILE
    })
  })
}

// //领用管理  获取领用单详情信息
export const handleGetDetailRecord = (reqData) => (dispatch) => {
  dispatch({type: actionType.REQUISITIONSORDERS_UPDATE, data: {tableLoading: true}})
  getData({api: API_REQUISITIONSDTLORDERS, ...reqData}).then(data => {
    console.log(data)
    // 相应的逻辑判断
    dispatch({
      type: actionType.REQUISITIONSORDERS_UPDATE,
      data: {
        tableLoading: false,
        searchResultRecord: data.result || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.REQUISITIONSORDERS_FILE
    })
  })
}

// 领用管理  新增领用单
export const handleCommit = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.REQUISITIONSORDERS_UPDATE, data: {tableLoading: true}})
  let api = API_CREATEREQUISITIONSORDERS_CREATE
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.REQUISITIONSORDERS_UPDATE,
      data: {
        tableLoading: false
      }
    })
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.REQUISITIONSORDERS_FILE
    })
  })
}

//领用导出列表
export const handleExport = (reqData, callBack) => (dispatch) => {
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  if (reqObj.initDate && reqObj.initDate.length) {
    reqObj.beginDate = moment(reqObj.initDate[0]).format('YYYY-MM-DD')
    reqObj.endDate = moment(reqObj.initDate[1]).format('YYYY-MM-DD')
  }
  delete reqObj.initDate
  getData({api: API_REQUISITIONSORDERS_EXPORT, ...reqData}).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.REQUISITIONSORDERS_FILE
    })
  })
}

//冲销功能
export const handleFix = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.REQUISITIONSORDERS_UPDATE, data: {buttonLoading: true}})
  getData({api: API_FIX_REQUISITIONWRITEOFF, ...reqData}).then(data => {
    dispatch({
      type: actionType.REQUISITIONSORDERS_UPDATE,
      data: {
        buttonLoading: false
      }
    })
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.REQUISITIONSORDERS_FILE
    })
  })
}

// 获取页面按钮权限
export const GetButtonPower = (reqData) => (dispatch) => {
  getData({api: API_GET_MENUS_PERMISSIONS_BANS, ...reqData}).then(({data}) => {
    console.log(data)
    // 相应的逻辑判断
    let Power = {}
    data.map((o) => {
      Power[o.menuCode] = true
    })
    dispatch({
      type: actionType.REQUISITIONSORDERS_UPDATE,
      data: {
        ...Power
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.REQUISITIONSORDERS_FILE
    })
  })
}



