import React, {PureComponent} from 'react'
import {
  Modal,
  Table,
  Row,
  Col
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.reqManagement, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
      
    }
    this.columns = [{
      title: '商品编号',
      width: 100,
      dataIndex: 'productCode',
      key: 'productCode'
    }, {
      title: '商品名称',
      width: 100,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '商品条码',
      width: 100,
      dataIndex: 'productBarCode',
      key: 'productBarCode'
    }, {
      title: '领用数量',
      width: 100,
      dataIndex: 'requisitionsQty',
      key: 'requisitionsQty'
    }, {
      title: '单价（元）',
      width: 100,
      dataIndex: 'productPrice',
      key: 'productPrice'
    }, {
      title: '总价（元）',
      width: 100,
      dataIndex: 'requisitionsAmount',
      key: 'requisitionsAmount'
    }]
  }
  componentDidMount() {
    this._getAsyncData()
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData() {
    const {handleGetDetailRecord, match: {params}, history} = this.props
    console.log(params.id)
    handleGetDetailRecord({searchParams: {orderCode : params.id}})
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {history} = this.props
    history.length > 1 ? history.goBack() : window.close()
  }

  render() {
    const {
      tableRecordLoading, 
      searchResultRecord,
      orderCode,
      shipperName,
      requisitionsTypeName,
      requisitionsTotalAmount,
      remark, 
    } = this.props
    let showData = searchResultRecord.header?searchResultRecord.header:''
    return (
        <div className='fullOrderDel'>
          <span className='ant-modal-close-x close' onClick={this.handleCloseModal}></span>
          <div style={{paddingLeft: 20}}>
            <Row>
              <Col span={8}><h4>货主：{showData.shipperName}</h4></Col>
              <Col span={8}><h4>领用类型：{showData.requisitionsTypeName}</h4></Col>
              <Col span={8}><h4>领用总额：{showData.requisitionsTotalAmount}</h4></Col>
              <Col span={24}><h4>备用：{showData.remark}</h4></Col>
            </Row>
          </div>
          <div className='table-group' style={{margin: 0}}>
            <Table
              rowKey={record => record.id}
              loading={tableRecordLoading}
              columns={this.columns}
              scroll={{x: 900, y: 400}}
              dataSource={searchResultRecord.item}
              pagination={false}
            />
          </div>
        </div>
    )
  }
}



